* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-container {
    width: 100px;
    float: right;
}
.App {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  background-color: #fff;
}

.navbar {
  width: 100%;
  background: #ccc;
}

.navbar h1 {
  font-weight: 400;
  text-align: center;
}

.navbar h1 span {
  font-weight: 700;
}

.add-reservation {
  margin: 20px 0 10px;
}

.add-reservation h2 {
  text-align: center;
}

.res-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.res-form input {
  border: none;
  border-radius: 3px;
  font-size: 16px;
}

.res-form-label {
  padding: 0 10px 0 0;
}

.res-form input[type=submit] {
  background-color: green;
  color: white;
  padding: 5px 10px;
  margin: 10px auto;
  font-size: 14px;
}

.res-form input:focus {
  outline: black;
}

.res-list {
  width: 50%;
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.res-list h2 {
  font-size: 0.8;
}

/* each list item */
.list-item {
  border: 1px solid black;
  border-radius: 3px;
  margin: 10px auto;
  padding: 0 5px 0 0;
}

.res-list table {
  width: 100%;
}

.remove-btn-container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 10px 0;
}

.remove-btn {
  color: white;
  background-color: red;
  border: none;
  padding: 5px 10px;
  border-radius: 3px;
  font-size: 14px;
}
